document.onkeydown = checkKey;
var fullkey = 0;
function checkKey(e) {

    e = e || window.event;

    if (e.keyCode == '192') {
         
	 fullkey++;
        if(fullkey==4){
   document.getElementsByClassName("nav")[0].style.display="block";     
            document.getElementsByClassName("img-container")[0].style.height="80%"; 
            document.getElementsByClassName("img-container")[0].style.position="static"; 
        
        }
    }
   

}

var noSleep = new NoSleep();






var isPlaying = false;


var myIndex = 0;
var myBgArray =[""];



var currentSong = 0;


import {
    onDestroy
} from 'svelte';
onDestroy(() => {
    s1.unsubscribe();
})
let routerparams
$: RouteParams = routerparams

let s1 = APPSTORE.AppRouterStore.subscribe(function(data) {
    if (data !== undefined) {
        routerparams = data;
        console.log(data.path, `form >> ${data.params.albumId}`)

loadAlbum(data.params.albumId);




    }

})


var songData;

function getData(res){

    songData = (JSON.parse(res)).songs;

loadAudio(songData[0].song);
myBgArray = songData[0].images;

}



var sBrowser, sUsrAg = navigator.userAgent;
if (sUsrAg.indexOf("Firefox") > -1) {
    sBrowser = "FIREFOX";
    // "Mozilla/5.0 (X11; Ubuntu; Linux x86_64; rv:61.0) Gecko/20100101 Firefox/61.0"
} else if (sUsrAg.indexOf("Opera") > -1 || sUsrAg.indexOf("OPR") > -1) {
    sBrowser = "OPERA";
    //"Mozilla/5.0 (Macintosh; Intel Mac OS X 10_14_0) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/70.0.3538.102 Safari/537.36 OPR/57.0.3098.106"
} else if (sUsrAg.indexOf("Trident") > -1) {
    sBrowser = "IE";
    // "Mozilla/5.0 (Windows NT 10.0; WOW64; Trident/7.0; .NET4.0C; .NET4.0E; Zoom 3.6.0; wbx 1.0.0; rv:11.0) like Gecko"
} else if (sUsrAg.indexOf("Edge") > -1) {
    sBrowser = "EDGE";
    // "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/58.0.3029.110 Safari/537.36 Edge/16.16299"
} else if (sUsrAg.indexOf("Chrome") > -1) {
    sBrowser = "CHROME";
    // "Mozilla/5.0 (X11; Linux x86_64) AppleWebKit/537.36 (KHTML, like Gecko) Ubuntu Chromium/66.0.3359.181 Chrome/66.0.3359.181 Safari/537.36"
} else if (sUsrAg.indexOf("Safari") > -1) {
    sBrowser = "SAFARI";
    // "Mozilla/5.0 (iPhone; CPU iPhone OS 11_4 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/11.0 Mobile/15E148 Safari/604.1 980x1306"
} else {
    sBrowser = "unknown";
}



var mediaToload;
 var audio = document.getElementById('audio');

function loadAudio(song) {

    var audioSource = document.getElementById('audio-source'); 
    
    if (sBrowser == "FIREFOX") {
       
        mediaToload = "assets/media/"+song+".ogg"
     } else if (sBrowser == "CHROME" || sBrowser == "IE" || sBrowser == "EDGE") {
       
         mediaToload = "assets/media/"+song+".mp3"
     }

    setTimeout(function(){
        audio = document.getElementById('audio');
        var source = document.getElementById('audioSource');
        source.src = mediaToload   
        audio.load(); 
audio.onended = function() {

if(currentSong<songData.length-1){
    currentSong+=1;
    loadAudio(songData[currentSong].song);
myBgArray = songData[currentSong].images;

}

else{
  currentSong=0;
    loadAudio(songData[currentSong].song);
myBgArray = songData[currentSong].images;

}


    
};



        if(isPlayedOnetime){
             audio.play();
        isPlaying = true;
        }
        
       
      

    },1000) 

}



var isPlayedOnetime = false;

function loadAlbum(album){



 AppSERVICES.ajaxSvc({
        type: "GET",       
        url: `assets/data/${album}.json?dynamic=00`,
        onData: getData
      });


}

var clipOpacityX;
var clipSetX;
var bgSizeX = 125;
var currentBgX = "bg.jpg";
$: clipSet$ = clipSetX
$: clipOpacity$ = clipOpacityX;
$: currentBg$ = currentBgX;

$: bgSize$ = bgSizeX

function startIt() {

    var a = 50;
    var b = 50;

    var xx = 0;
    bgSizeX = 125;
    //alert( myBgArray[myIndex])

    currentBgX = myBgArray[myIndex];

    //alert(myBgArray[myIndex])

    console.log(myBgArray[myIndex])

    var startSlide = setInterval(function() {

        if (xx > 0.9900 && bgSizeX < 100.01) {
            xx = 0;
            clearInterval(startSlide)
            if (myIndex < myBgArray.length - 1) {
                myIndex = myIndex + 1;
            } else {
                myIndex = 0
            }

            startIt();

        }

        if (a > 0 && b > 0) {
            a = a - .5;
            b = b - .5;
        }

        if (xx < 1) {
            xx = xx + .01;

        }

        if (bgSizeX > 100) {
            bgSizeX = bgSizeX - .1;
        }

        clipSetX = `${a}% ${b}%`;

        clipOpacityX = xx;

    }, 50)

}

startIt();

function resetShowControlTimeOut(){
    clearControlTimeout()
    setControlTimeout();
}

clipOpacity$ = clipOpacityX;

  var showControl = true;   
     
function playSong(){
    isPlayedOnetime = true;
    isPlaying= true;
    resetShowControlTimeOut();
    audio.play();
    noSleep.enable();  
    document.documentElement.requestFullscreen(); 
  

}

function pauseSong(){
    audio.pause();
    isPlaying = false;
  resetShowControlTimeOut()
  

  

}

function lastSong(){
    checkAudioPlay();
    resetShowControlTimeOut();
 if(currentSong<0){

    currentSong-=1;
    loadAudio(songData[currentSong].song);
myBgArray = songData[currentSong].images;

}

else{
 currentSong=0;
    loadAudio(songData[currentSong].song);
myBgArray = songData[currentSong].images;

}
}

function nextSong(){
    checkAudioPlay()
    resetShowControlTimeOut();


if(currentSong<songData.length-1){

    currentSong+=1;
    loadAudio(songData[currentSong].song);
myBgArray = songData[currentSong].images;

}

else{
 currentSong=0;
    loadAudio(songData[currentSong].song);
myBgArray = songData[currentSong].images;

}


}







function checkAudioPlay(){

if(audio.paused){
    isPlaying = false;
}
else {
  isPlaying = true;
}

}

function showSongControl(){
    if(showControl){showControl=false}
    else{showControl=true}


  resetShowControlTimeOut();


}

var toHideControl;

function setControlTimeout(){
toHideControl = setTimeout(function(){    
    showControl=false}, 10000)

}

function clearControlTimeout(){
 clearTimeout(toHideControl);

}





		</script>



