}

.song-container{
	background-image: url("assets/assets/images/bg.jpg");
	background-position: center;
	background-size: auto;
	background-repeat: no-repeat;
	height: 700px;
	    background-color: #000;

}

.play-ctrl-holder{
	display: block;
    width: 100px;
    height: 100px;
    background: rebeccapurple;
    margin-left: 50%;
    margin-top: 19px;
    position: relative;
}


.clipboard {
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;  
    background-size: cover;
    background-position: center center;
}


.audio-ctrl {

    display:block;
    height:auto;
    width:100%;
    background-color:#d3d3d3;
    position:absolute;
    top:50%;
    opacity: .7;
}

.icon-extra-large {
    width:70px;
    height:70px
}
		

	
    .hide{
        display:none;
    }

    .show{
        display:block;
    }
	
.next-song:active, .last-song:active {color:orange}
	
    </style>
	
	
	
	
			<audio id="audio" >
  <source id="audioSource" src="">
  Your browser does not support the audio format.
</audio>
	
    

		<div on:click={showSongControl}  class="clipboard" style="opacity:{clipOpacity$}; background-size: {bgSize$}%; background-image: url({currentBg$})">  
       
      
       
            
        <img style="display:none" src="assets/images/bg.jpg">
        </div>

        <div class:show={showControl}  class:hide={!showControl} class="pure-g audio-ctrl">
    <div class="pure-u-1-3 last-song" id="last-song" on:click={lastSong}><i class="icon-extra-large" data-feather="skip-back"></i></div>
    <div class="pure-u-1-3" id="play-song" >
    
    <span class="play-song-play" class:show={!isPlaying}  class:hide={isPlaying} on:click={playSong} ><i  class="icon-extra-large" data-feather="play-circle"></i></span>
    
   
   <span class="play-song-pause" class:show={isPlaying} class:hide={!isPlaying} on:click={pauseSong}> <i  class="icon-extra-large" data-feather="pause-circle"></i></span>
</div>
    <div class="pure-u-1-3 next-song" id="next-song" on:click={nextSong}><i class="icon-extra-large" data-feather="skip-forward"></i></div>
</div>