<script>
import Header_component from './header/header.component.svelte';
import Footer_component from './footer/footer.component.svelte';
import Dashboard_page_component from './dashboard/dashboard-page.component.svelte';
import About_page_component from './about/about-page.component.svelte';
import Nested_page_component from './nested-page/nested-page.component.svelte';
const appComponents = {
"Header_component" : Header_component,
"Dashboard_page_component" :Dashboard_page_component,
"About_page_component" :About_page_component,
"Nested_page_component": Nested_page_component
}

import {
    PageRoutes
} from '../scripts/page-routes.ts';
import * as NavigoRouter from '../scripts/vendor/navigo.js';

import {AppRouteActions} from './app.route-actions.ts';
    import { onMount, afterUpdate } from 'svelte';
    




afterUpdate(() => {
		window.feather.replace()
});






const Routes = new PageRoutes();
const pageRoutes = Routes.getPageRoutes();

const Navigo = NavigoRouter.default;
let pageContent;
$: ViewComponent = pageContent;

let contentProps;
$: pageProps = contentProps;
contentProps = {}

let pageHeader;


function loadComponent(Cname, routerStateData) {      
     pageContent = appComponents[Cname];
     APPSTORE.AppRouterReducer.next({
                type: AppRouteActions.ROUTE_LOADED,
                payload: routerStateData
            });

    }


var router = new Navigo(null, true, '#');
router.resolve();
var routeList = pageRoutes;
routeList.forEach((rout) => {
    router.on(rout['path'], (params, query) => {

        let routerStateData = {
            routeData: rout,
            params: params,
            query: query
        }

    
        var componentName = rout.component;
        if (window[componentName] == undefined) {
            loadComponent( componentName, routerStateData);
        } else {
            pageContent = window[componentName];            
            APPSTORE.AppRouterReducer.next({
                type: AppRouteActions.ROUTE_LOADED,
                payload: routerStateData
            });

        }

    }).resolve();
})

router.on(() => {
 router.navigate('/home');

}).resolve();
router.notFound((query) => {
    document.getElementById('view').innerHTML = '<h3>Couldn\'t find the page you\'re looking for...</h3>';
    });
router.resolve();
</script>
<style global type="text/postcss" >
@import "../css/lib/cirrus.min.css";
@import "../css/lib/pure-min.css";
#view{ background-color: #000000!important;}
</style>

<main>

 	<div id="header">
	<Header_component  routeList={pageRoutes} /></div>
<div id="view">
<svelte:component this={ViewComponent}  />
</div>

<div id="footer">
	<Footer_component/>
	</div>

</main>
    